import React, { FC } from 'react';
import { PolygonProps } from './Polygon.types';
import { ngon, rad, Cubic, subtract, add } from '../../geometry';
import { FaBeer } from 'react-icons/fa';
import { Svg, Path } from '../Svg';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fill = '#000',
	stroke = '#000',
	strokeWidth = 0.1,
	children = <FaBeer/>
}) => {
	const [points, viewBox] = ngon(sides, scale, rad(rotation), cornerRadius, [0, 0]);
	const scaledLineWidth = strokeWidth * scale;
	const hl = scaledLineWidth > 0 ? scaledLineWidth/2:0;
	const newViewBox:Cubic = [viewBox[0]-hl, viewBox[1]-hl, viewBox[2]+scaledLineWidth, viewBox[3]+scaledLineWidth];
	return (<Svg 
	viewBox={[...subtract([viewBox[0], viewBox[1]], hl), ...add([viewBox[2], viewBox[3]], scaledLineWidth)]} 
	width='100px' 
	height='100px'>
		<Path {...{points, fill, stroke, strokeWidth, scale}}/>
		{children}
	</Svg>);
};